<template>
  <div
    class="com-button"
    :class="checkProps"
    @click="onClick"
  >
    {{text}}
  </div>
</template>

<script>
export default {
  name: 'com-button',
  props: {
    text: String,
    size: String,
    type: String,
    isblock: Boolean,
    disable: Boolean
  },
  computed: {
    checkProps() {
      let className = ''
      let props = this.$options.propsData
      for (let key in props) {
        if ((key === 'size' || key === 'type') && props[key]) {
          className += ` ${props[key]}`
        }
        if (key === 'disable' || key === 'isblock') {
          className += ` ${key}`
        }
      }
      return className
    }
  },
  methods: {
    onClick(event) {
      if (!this.disable) {
        this.$emit('click', event)
      }
    }
  }
}
</script>

<style scoped lang='less'>
@import '../../assets/css/var';
.com-button {
  display: inline-block;
  width: 160px;
  height: 44px;
  line-height: 44px;
  border-radius: 22px;
  font-size: 17px;
  color: @white;
  letter-spacing: 0;
  text-align: center;
  background-color: @blue;
  &.isblock {
    display: block;
  }
  // size
  &.biggest {
    width: 100%;
  }
  &.large {
    width: 189px;
  }
  // type
  &.cancle {
    background-color: @white;
    color: @red;
  }
  &.disable {
    background-color: @btn-disable;
  }
}
</style>
