import axios from 'axios'
import {
  urlEncode
} from '../util'

// config
const baseURL = process.env.NODE_ENV === 'production' ? 'http://daishu.com' : 'https://mock.kaier33.top'
axios.defaults.timeout = 5000
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
axios.defaults.baseURL = baseURL
// console.log(baseURL)
export function $get({
  url = '',
  param = {}
}) {
  if (param) {
    url += urlEncode(param)
  }
  return new Promise((resolve, reject) => {
    axios.get(url)
      .then((res) => {
        resolve(res)
      })
      .catch((err) => {
        reject(err)
      })
  })
}

export function $post({
  url = '',
  param = {},
  data = {}
}) {
  if (param) {
    url += urlEncode(param)
  }
  return new Promise((resolve, reject) => {
    axios.post(url, data)
      .then((res) => {
        resolve(res)
      })
      .catch((err) => {
        reject(err)
      })
  })
}
