<template>
  <div class="home">
    <HelloWorld msg="Welcome to Your Vue.js App"/>
    <ComButton :text="'默认按钮'"  @click="one"/>
    <ComButton :text="'块按钮'"  @click="one" isblock />
    <ComButton :text="'取消按钮'" :type="'cancle'" :size="'large'" @click="two"/>
    <ComButton :text="'禁用按钮'" disable :size="'biggest'" @click="three"/>

    <router-link to="/custom-add-items">custom-add-items</router-link>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import ComButton from '@/components/common/button.vue'
import { getUserInfo, getHospitalinfo } from '../assets/js/http/api'
export default {
  name: 'home',
  components: {
    HelloWorld,
    ComButton
  },
  methods: {
    one() {
      this.$get({ url: getUserInfo })
        .then((res) => {
          console.log(res)
        }, (err) => {
          console.log(err)
        })
    },
    two() {
      this.$post({ url: getHospitalinfo, data: { city: 'shenzhen' } })
        .then((res) => {
          console.log(res)
        }, (err) => {
          console.log(err)
        })
    },
    three() {
      console.log(789)
    }
  }
}
</script>
